if (!String.prototype.format) {
	String.prototype.format = function() {
		var str = this.toString();
		if (!arguments.length)
			return str;
		var args = typeof arguments[0];
		args = (("string" == args || "number" == args) ? arguments
				: arguments[0]);
		for ( var arg in args)
			str = str.replace(RegExp("\\{" + arg + "\\}", "gi"), args[arg]);
		return str;
	}
}

angular.module('dziws', [
        'ngRoute',
        'ngAnimate',
        'angularMoment',
        'home',
        'about',
        'service',
        'consulting',
        'products',
        'support',
        'blog',
        'contact',
        'login',
        'error',
        'adminpanel',
        'ngWig'
        ])
   .config(['$locationProvider', '$routeProvider', function($locationProvider, $urlRouterProvider) {
		  $urlRouterProvider.otherwise('/');
   }])
   .run(['$rootScope', '$location', '$anchorScroll', '$routeParams', 'messages', function($rootScope, $location, $anchorScroll, $routeParams, messages) {
		$rootScope.messages = messages;

		//when the route is changed scroll to the proper element.
		$rootScope.$on('$routeChangeSuccess', function(newRoute, oldRoute) {
			$location.hash($routeParams.scrollTo);
			$anchorScroll();  
		});
	}])
	.directive('focus', ['$timeout', function($timeout) {
		return {
	        restrict : 'A',
	        link : function($scope,$element,$attr) {
	            $scope.$watch($attr.focus,function(_focusVal) {
	                $timeout(function() {
	                    _focusVal ? $element.focus() :
	                        $element.blur();
	                });
	            });
	        }
	    };
	}]);
